.movie {
	animation: 0.5s ease-out 0s 1 zoomIn;
	height: 500px;
	transition: all 0.5s ease;
	&:hover {
		transform: scale(1.1);
		box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.404);
	}
}

.movie__container {
	height: 650px;
	&:hover .movie:not(:hover) {
		transform: scale(0.95);
		filter: grayscale(100%);
	}
}
